<template>
  <Loading :status="loadingStatus"></Loading>
  <section class="container pageContent py-5">
    <h1 class="text-center fw-bold m-0 pb-5"></h1>
    <div class="row justify-content-between pb-2">
      <span class="col-md-4 col-lg-2 pb-1">
        <select
          class="form-select"
          @change="changeProduct"
          v-model="select"
        >
          <option value="" selected></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </span>
      <span class="col-lg-2 text-end pb-1">
        <button
          class="btn btn-outline-brown"
          @click="openModal(true)"
        ></button>
      </span>
    </div>

    <table
      class="table table-hover text-center text-break
        rounded overflow-hidden shadow-sm"
    >
      <thead class="table-dark align-middle">
        <tr>
          <td width="9%"></td>
          <td width="7%"></td>
          <td width="13%">ID</td>
          <td width="12%"></td>
          <td width="12%"></td>
          <td width="12%"></td>
          <td width="7%"></td>
          <td width="7%"></td>
          <td width="7%"></td>
          <td width="7%"></td>
          <td width="7%"></td>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="product in products"
          :key="product.id"
        >

          <td data-title="">
            {{ product.category }}
          </td>

          <td data-title="">
            <img
              class="w-100"
              :src="product.imageUrl"
              alt="product.title"
            />
          </td>

          <td data-title="ID">
            {{ product.id }}
          </td>

          <td data-title="">
            {{ product.title }}
          </td>

          <td data-title="">
            {{ product.description }}
          </td>
          <td data-title="">
            {{ product.content }}
          </td>

          <td data-title="">
            {{ product.origin_price }}
          </td>

          <td data-title="">
            {{ product.price }}
          </td>

          <td
            data-title=""
            :class="{ 'text-gray' : !product.is_enabled }"
          >
            {{ product.is_enabled ? '' : '' }}
          </td>

          <td data-title="">
            <button
              class="btn btn-outline-brown p-1"
              @click="openModal(false, product)"
            ></button>
          </td>

          <td data-title="">
            <i
              class="bi bi-trash-fill btn btn-outline-danger"
              @click="delProductFn(product)"
            ></i>
          </td>
          <!-- {{ product }} -->

        </tr>
      </tbody>
    </table>
    <Pagination
      :pagination-page="productPagination"
      @get-data="getProducts"
    ></Pagination>

    <ProductModal
      ref="productModal"
      :modal-product="tempProduct"
      :modal-isNew="isNew"
      @modal-update-product="updateProduct"
    ></ProductModal>

  </section>
</template>

<script>
import { swalFn, delSwalFn } from '@/methods/swal';
import Pagination from '@/components/DashboarPagination.vue';
import ProductModal from '@/components/DashboarProductModal.vue';

export default {
  name: 'DashboardProducts',
  data() {
    return {
      loadingStatus: false,
      select: '',
      products: {},
      productPagination: {},
      tempProduct: {},
      isNew: Boolean,
    };
  },
  components: { Pagination, ProductModal },
  methods: {
    getProducts(page = 1) { // 
      const url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/admin/products?page=${page}`;
      this.loadingStatus = true;

      this.$http.get(url)
        .then((res) => {
          if (res.data.success) {
            console.log('(-) res', res);
            this.productPagination = res.data.pagination;
            this.products = res.data.products;
            this.loadingStatus = false;
            console.log('(-) vue', this.productPagination, this.products);
          } else {
            console.log('(-) res', res);
            this.loadingStatus = false;
          }
        })
        .catch((err) => {
          console.log('(-) err');
          console.dir(err);
          this.loadingStatus = false;
        });
    },
    delProduct(id) { // 
      const url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/admin/product/${id}`;
      this.loadingStatus = true;

      this.$http.delete(url)
        .then((res) => {
          if (res.data.success) {
            swalFn(res.data.message, 'success');
            this.getProducts();
            this.loadingStatus = false;
          } else {
            swalFn(res.data.message, 'error');
            this.loadingStatus = false;
          }
        })
        .catch((err) => {
          console.log('(-) err');
          console.dir(err);
          this.loadingStatus = false;
        });
    },
    delProductFn(data) { // 
      const { title, id } = data;
      delSwalFn(title, id, this.delProduct);
    },
    openModal(isNew, product) { // 
      if (isNew) {
        this.isNew = true;
        this.tempProduct = { category: '' };
      } else {
        this.isNew = false;
        this.tempProduct = product;
      }
      this.$refs.productModal.openModal();
    },
    updateProduct(item) { //  & 
      this.loadingStatus = true;
      let url = '';
      let httpMethods = '';

      if (this.isNew) {
        url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/admin/product`;
        httpMethods = 'post';
      } else {
        url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/admin/product/${item.id}`;
        httpMethods = 'put';
      }

      this.$http[httpMethods](url, { data: item })
        .then((res) => {
          if (res.data.success) {
            console.log('(-) res', res);
            swalFn(res.data.message, 'success');
            this.getProducts();
            this.$refs.productModal.hideModal();
          } else {
            console.log('(-) res', res);
            swalFn(res.data.message, 'error');
            this.loadingStatus = false;
          }
        })
        .catch((err) => {
          console.log('(-) err');
          console.dir(err);
          this.loadingStatus = false;
        });
    },
  },
  mounted() {
    this.getProducts();
    this.select = '';
  },
};
</script>
